import "core-js/modules/es6.string.link";
import wx from 'wx-sdk-ts';
import axios from "axios";
var wxShareModel = {
  getShareConfig: function getShareConfig() {
    axios({
      url: 'http://wcx.free.idcfengye.com/api/redEnvelope/getMsfWechatConfig',
      method: 'POST',
      params: {
        url: window.location.href
      }
    }).then(function (res) {
      console.log(res);
      var data = res.data;
      wx.config({
        debug: true,
        appId: data.appId,
        timestamp: data.timestamp,
        nonceStr: data.noncestr,
        signature: data.signature,
        jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage", "onMenuShareQQ", "onMenuShareWeibo", "onMenuShareQZone"]
      });
    });
    return this;
  },
  setShareConfig: function setShareConfig(wxConfig) {
    wx.ready(function () {
      wx.checkJsApi({
        jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage", "onMenuShareQQ", "onMenuShareWeibo", "onMenuShareQZone"],
        success: function success(res) {
          console.log(JSON.stringify(res));
        }
      }); // 分享到朋友圈

      wx.onMenuShareTimeline({
        title: wxConfig.title,
        link: wxConfig.link,
        imgUrl: wxConfig.imgUrl,
        success: function success() {}
      }); // 分享给朋友

      wx.onMenuShareAppMessage({
        title: wxConfig.title,
        desc: wxConfig.desc,
        link: wxConfig.link,
        imgUrl: wxConfig.imgUrl,
        success: function success() {}
      }); // 分享到QQ

      wx.onMenuShareQQ({
        title: wxConfig.title,
        desc: wxConfig.desc,
        link: wxConfig.link,
        imgUrl: wxConfig.imgUrl
      }); // 分享到腾讯微博

      wx.onMenuShareWeibo({
        title: wxConfig.title,
        desc: wxConfig.desc,
        link: wxConfig.link,
        imgUrl: wxConfig.imgUrl
      }); // 分享到QQ空间

      wx.onMenuShareQZone({
        title: wxConfig.title,
        desc: wxConfig.desc,
        link: wxConfig.link,
        imgUrl: wxConfig.imgUrl
      });
      wx.error(function (res) {});
    });
  }
};
export var wxShare = wxShareModel.getShareConfig().setShareConfig;